
import { AtmosDust, AtmosUv } from "@/store/src/state"
import { defineComponent, PropType, ref } from "@vue/runtime-core"

export default defineComponent({
  props: {
    data: {
      type: Object as PropType<{ dust: Array<AtmosDust>; uv: AtmosUv }>,
      required: true,
    },
  },
  setup(props) {
    const env = ref(props.data)

    const value = (val: string) => {
      switch (val) {
        case "1":
          return "좋음"
        case "2":
          return "보통"
        case "3":
          return "나쁨"
        case "4":
          return "매우 나쁨"
        default:
          return "좋음"
      }
    }

    const uvValue = (val: string) => {
      const key = parseInt(val, 10)
      if (key <= 2) {
        return "낮음"
      } else if (3 <= key && key <= 5) {
        return "보통"
      } else if (6 <= key && key <= 7) {
        return "높음"
      } else if (8 <= key && key <= 10) {
        return "매우 높음"
      } else if (11 <= key) {
        return "위험"
      }
    }
    const dustVal = ref(value(props.data.dust[0].pm10Grade1h))
    const minDustVal = ref(value(props.data.dust[0].pm25Grade1h))
    const o3Val = ref(uvValue(props.data.dust[0].o3Grade))
    const uvVal = ref(uvValue(props.data.uv))

    return { env, dustVal, minDustVal, o3Val, uvVal }
  },
})
